  font-weight: 600;
  font-size: 16px;
  padding: 0.618em 2em;
  text-decoration: none;
  white-space: nowrap;
  display: inline-block;
  width: auto;
  height: auto;
  box-shadow: inset 0 0 0 999px rgba(255, 255, 255, 0);
  line-height: 1.618;
}

