    display: none;
  }
}

.responsive .genesis-nav-menu .menu-item {
  width: 100%;
  padding: 0;
}

@media (min-width: 768px) {
  .responsive .genesis-nav-menu .menu-item {
    width: auto;
  }
}

.genesis-nav-menu a {
  text-decoration: none;
  display: block;
  padding: 0.5em 0;
}

@media (min-width: 768px) {
  .genesis-nav-menu a {
    padding: 3em 1em;
  }
}

@media (min-width: 1023px) {
  .genesis-nav-menu a {
    padding: 3.333333333em 1.2em;
  }
}

.genesis-nav-menu a:hover,
.genesis-nav-menu .current-menu-item > a,
.genesis-nav-menu .sub-menu .current-menu-item > a:hover {
  text-decoration: none;
  color: #2c2d33;
}

@media (min-width: 768px) {
  .genesis-nav-menu > .menu-item-has-children:hover > a:after {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.9s ease;
  }
  .site-footer .genesis-nav-menu > .menu-item-has-children:hover > a:after {
    display: none;
  }
}

.genesis-nav-menu > .menu-item-has-children > a {
  position: relative;
}

.genesis-nav-menu > .menu-item-has-children > a:after {
  content: " ";
  display: block;
  position: absolute;
  bottom: -5px;
  bottom: -0.5rem;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 10px;
  height: 1rem;
  width: 10px;
  width: 1rem;
  background-color: #ffffff;
  border: 1px solid rgba(0, 10, 20, 0.075);
  border-right: none;
  border-bottom: none;
  transform: rotate(45deg);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0s ease;
}

.genesis-nav-menu > .menu-item-has-children > a span {
  padding-right: 0.382em;
  position: relative;
}

.genesis-nav-menu > .menu-item-has-children > a span:after {
  content: "\f0d7";
  font-family: 'FontAwesome';
  position: absolute;
  top: calc( 50% - 0.382em);
  line-height: 1em;
  margin-left: 0.5em;
  font-size: 6.112px;
  display: none;
}

@media (min-width: 768px) {
  .genesis-nav-menu > .menu-item-has-children > a span:after {
    display: inline-block;
  }
}

.site-footer .genesis-nav-menu > .menu-item-has-children > a span:after {
  display: none;
}

.genesis-nav-menu .sub-menu {
  display: none;
  transition: opacity 0.25s ease;
  clear: both;
}

.genesis-nav-menu .sub-menu:before, .genesis-nav-menu .sub-menu:after {
  display: block;
  content: '';
}

.genesis-nav-menu .sub-menu:after {
  clear: both;
}

@media (min-width: 768px) {
  .genesis-nav-menu .sub-menu {
    visibility: hidden;
    top: 100%;
    opacity: 0;
    position: absolute;
    width: 200px;
    z-index: 98;
    display: block;
    background-color: #ffffff;
    border: 1px solid rgba(0, 10, 20, 0.075);
    border-top: none;
    padding: 1em 0;
    box-shadow: inset 0 1px 0 rgba(0, 10, 20, 0.075);
  }
}

.site-footer .genesis-nav-menu .sub-menu {
  display: none;
}

.genesis-nav-menu .sub-menu .menu-item {
  width: 100%;
}

.genesis-nav-menu .sub-menu a {
  padding-left: 10px;
  text-align: left;
}

@media (min-width: 768px) {
  .genesis-nav-menu .sub-menu a {
    padding: 1em 1.382em;
    position: relative;
    word-wrap: break-word;
  }
}

@media (min-width: 768px) {
  .genesis-nav-menu .sub-menu .sub-menu {
    margin: -1px 0 0 199px;
    padding-left: 0;
    border: none;
    opacity: 0;
    visibility: hidden;
    border: 1px solid rgba(0, 10, 20, 0.075);
    box-shadow: none;
    left: -1px;
    top: -1em;
  }
  .genesis-nav-menu .sub-menu .sub-menu:before {
    display: none;
  }
}

.genesis-nav-menu .sub-menu > .menu-item-has-children > a span:after {
  content: '\f112';
  font-family: 'FontAwesome';
  position: absolute;
  top: calc( 50% - 0.618em);
  line-height: 1em;
  margin-left: 0.5em;
  font-size: 6.112px;
  display: none;
}

@media (min-width: 768px) {
  .genesis-nav-menu .sub-menu > .menu-item-has-children > a span:after {
