  }
}

@media (min-width: 768px) {
  .genesis-nav-menu .menu-item-has-children:hover > .sub-menu {
    opacity: 1;
    visibility: visible;
  }
}

.genesis-nav-menu > .first > a {
  padding-left: 0;
}

.genesis-nav-menu > .last > a {
  padding-right: 0;
}

.genesis-nav-menu > .right {
